* {
  box-sizing: border-box;
  a {
    font-family: 'Poppins', sans-serif;
  }
}
.app {
  text-align: center;
  color: rgba(86, 81, 81, 0.82);
  font-family: 'Poppins', sans-serif;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-content: space-between;
  width: 100%;
}

.main {
  position: relative;
  min-height: 90vh;

}

.mainComponents {
  padding-left: 5px;
  padding-right: 5px;
}


.modalWrap {
  margin: 0 auto;
  position: absolute;
  top:50%;
  left:50%;
  transform:translate(-50%, -50%);
  color: red;
  width: 80px;
  border: 1px solid red;
}

.modalHidden{
 display: none;
}
